@import url(https://fonts.googleapis.com/css2?family=Exo+2:wght@300&display=swap);
*, body {
  margin: 0;
  font-family: 'Exo 2', BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

:root {
    --theme-button-text: #FFF;
    --light:  rgb(238, 230, 223);
    --semidark: rgb(80, 80, 80);
    --dark: rgb(17, 17, 17);
    --accent-bright: rgb(255, 69, 0);
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

h1 {
    font-size: 6vh;
}

h3 {
    font-size: 3vh;
}

.light {
    --theme-page-background: var(--light);
    --theme-page-text: var(--dark);
    --theme-button-color: rgb(39, 60, 130);
    --theme-same: var(--light);
    --theme-diff: var(--dark);
    --theme-mid: var(--semidark);
}

.dark {
    --theme-page-background: rgb(24, 24, 24);
    --theme-page-text: var(--light);
    --theme-button-color: var(--accent-bright);
    --theme-same: var(--dark);
    --theme-diff: var(--light);
    --theme-mid: var(--semidark);
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  box-sizing: border-box;

  background: var(--theme-page-background);
  color: var(--theme-page-text);

  /* mask: url('./assets/bg_pattern.svg') repeat; */
}

.corner {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

button {
  padding: 0.75em;
  margin: 1.5em;
  border-radius: 10%;
  text-align: center;
  background-color: var(--theme-button-color);
  color: var(--theme-button-text);
  font-size: 1rem;
  border: 0 solid transparent;
}

button:hover {
  -webkit-filter: contrast(125%);
          filter: contrast(125%);
}

button:focus {
  outline: none;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .App {
    height: 100vh;
    /* transform: scale(0.8); */
    overflow: hidden;
  }

  .Header {
    margin-top: 4.5em !important;
    padding: 1.5em !important;
    transform: scale(0.60);
    transform-origin: top;
    /* height: 100vh; */
  }
  h1 {
    font-size: calc(2em + 6vw) !important;
  }

  h3 {
    font-size: calc(1em + 3vw)!important;
  }

  .badges {
    transform: scale(2.25) !important;
    padding-top: 2em;
  }

  .row {
    flex-direction: row;
    margin: 0;
    padding: 0;
  }
}

/* Small devices (portrait tablets and large phones, 600 and up) */
@media only screen and (min-width: 600px) {
  .App {
    /* transform: scale(1); */
    overflow: hidden;
  }

  .Header {
    margin: 2%;
    padding: 0.75em !important;
    align-items: center;
    justify-content: center;
    transform: scale(0.80);
    transform-origin: center;
  }

  .badges {
    /* display: grid; */
    /* flex-direction: column; */
    /* flex-wrap: wrap; */
    transform: scale(0.6);
    padding-top: 2em;
  }
  .row {
    margin: 0;
    padding:0;

  }
}

/* Medium devices (landscape tablets, 768px and up) */
