.bed {
    width: 150px;
    position: absolute;
    border-top: 10px solid var(--theme-mid);
    border-bottom: 5px solid transparent;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    transform: translate(20px, 175px);

}

/* hotend case */
.hotend {
    width: 45px;
    height: 45px;
    background-color: var(--theme-mid);
    /* filter: brightness(35%); */
    position: absolute;
    /* transform: translate(78px, -10px); */
    animation: move linear 8000ms;
    animation-name: move;
    animation-timing-function: linear;
    animation-duration: 8000ms;
    animation-direction: reverse;
    offset-path: path("M 102 -158 V -140.4 H 116.4 Q 121.2 -143.6 116.4 -138.8 H 82.8 Q 78 -134 82.8 -129.2 H 154.8 Q 159.6 -124.4 154.8 -119.6 H 44.4 Q 39.6 -114.8 44.4 -110 H 154.8 Q 159.6 -105.2 154.8 -100.4 H 44.4 Q 39.6 -95.6 44.4 -90.8 H 154.8 Q 159.6 -86 154.8 -81.2 H 42");
    offset-anchor: center;
    offset-rotate: 0deg;
}

@keyframes move {
  100% {
    offset-distance: 100%;
  }
}

/* hotend fan */
.hotend:before {
    content: '';
    width: 30px;
    height: 30px;
    position: absolute;
    background-color: var(--theme-mid);
    border: 2.5px dotted var(--light);
    -webkit-filter: contrast(55%);
            filter: contrast(55%);
    border-radius: 50%;
    transform: translate(5px, 5px);
}

/* hotend nozzle */
.hotend:after {
    content: '';
    width: 30px;
    height: 20px;
    position: absolute;
    background-color: var(--theme-diff);
    -webkit-filter: brightness(55%);
            filter: brightness(55%);
    -webkit-clip-path: polygon(
        0% 20%, 15% 20%,
        15% 0%, 85% 0%,
        85% 25%, 100% 25%,
        100% 60%, 80% 65%,
        65% 90%, 35% 90%,
        20% 65% , 0% 60%);
    clip-path: polygon(
        0% 20%, 15% 20%,
        15% 0%, 85% 0%,
        85% 25%, 100% 25%,
        100% 60%, 80% 65%,
        65% 90%, 35% 90%,
        20% 65% , 0% 60%);
    transform: translate(7.5px, 45px);
}

path {
  stroke-dasharray: 634.277;
  /* stroke-dashoffset: 634.277; */
  animation: draw linear 8000ms forwards;
  animation-direction: forwards;
}

@keyframes draw {
  from {
      stroke-dashoffset: 634.277;
  }
  to {
      stroke-dashoffset: 1268.554;
  }
}

